.hp-banner {
	position: relative;
	z-index: 1;
	display: flex;
	overflow: hidden;
	flex-direction: column;
	min-height: 100vh;

	@include _1399 {
		min-height: calc(100vh - 106px);
	}

	@include _979 {
		min-height: calc(100vh - 168px);
	}

	@include _768 {
		min-height: calc(100vh - 120px);
	}
}

.hp-banner-vidbg {
	position: absolute;
	z-index: -3;
	top: 0;
	left: 0;
	display: block;
	width: 0;
	height: 0;

	source {}
}

.hp-banner-vidbg::-webkit-media-controls {
	display: none;
}

.hp-banner-picbg {
	position: absolute;
	z-index: -2;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transition-duration: .3s;
	object-fit: cover;
	object-position: center center;

	&--hide {
		opacity: 0;
	}

	img {}
}

.container--hp-banner {
	display: flex;
	align-items: center;
	flex-direction: column;
	flex-grow: 1;
	justify-content: space-around;
	width: 900px;

	@include _979 {
		padding: 25px 0;
	}

	&::before {
		position: absolute;
		z-index: -1;
		top: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		content: '';
		opacity: 0.65;
		background: #2A2217;
	}
}


.hp-banner__small-title {
	text-align: center;
	color: #FFFFFF;
	font-size: 20px;
	line-height: 135%;
}

.hp-banner__big-title {
	margin-top: 26px;
	text-align: center;
	color: #FFFFFF;
	font-family: $ffc;
	font-size: 60px;
	font-weight: 200;
	line-height: 125%;

	@include _979 {
		margin-bottom: 30px;
	}

	@include _600 {
		font-size: 45px;

		br {
			display: none;
		}
	}

	@include _480 {
		font-size: 35px;
	}
}

.hp-banner__play {
	cursor: pointer;
	transition-duration: .2s;

	@include _979 {
		height: auto;
		max-height: 150px;
		margin: 0 0 40px;

		&--hide {
			max-height: 0;
			margin: 0;
		}
	}

	&:hover {
		opacity: .7;
	}

	&--hide {
		opacity: 0;

		&:hover {
			opacity: 0;
		}
	}

	svg {}
}

.hpb-clinics {
	display: flex;
	justify-content: space-between;
	width: 100%;

	@include _979 {
		flex-wrap: wrap;
	}
}

.hpb-clinic {
	display: flex;
	align-items: center;
	flex-direction: column;

	@include _979 {
		flex-grow: 1;
		width: 50%;
		margin-bottom: 20px;
	}

	@include _600 {
		width: 100%;
	}

	&__img {
		width: 42px;
		height: 42px;

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: contain;
			object-position: center center;
		}
	}

	&__name {
		margin-top: 12px;
		text-align: center;
		color: #FFFFFF;
		font-size: 18px;
		font-weight: 500;
		line-height: 135%;
	}

	&__phone {
		margin-top: 15px;
		transition-duration: .2s;
		text-align: center;
		color: #FFFFFF;
		font-size: 24px;
		font-weight: 600;
		line-height: 135%;

		&:hover {
			opacity: .7;
		}
	}

	img {}
}

.hpb-scrollme {
	transition: all 0.5s ease;
	animation: scroll-me-moves 2s linear infinite;

	@include _979 {
		margin-top: 15px;
	}

	svg {}
}

@keyframes scroll-me-moves {
	0% {
		transform: translateY(30%);
	}

	50% {
		transform: translateY(-30%);
	}

	100% {
		transform: translateY(30%);
	}
}

