.doctors{
	&__container{
		margin-top: 48px;
	}

	&__slider{
		margin-left: -15px;
		margin-right: -15px;
		margin-top: 44px;
		.slick-slide{
			padding: 0 15px;
		}

		.slick-dots{
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: 48px;
			li{
				width: 24px;
				height: 24px;
				border: 1px solid #878183;
				box-sizing: border-box;
				list-style: none;
				border-radius: 50%;
				margin: 0 12px;
				position: relative;
				button{
					font-size: 0;
					padding: 0;
					margin: 0;
					width: 100%;
					height: 100%;
					display: block;
					cursor: pointer;
				}
				&::after{
					content: "";
					display: block;
					width: 100%;
					height: 100%;
					background: #C31D1F;
					border-radius: 50%;
					position: absolute;
					left: 0;
					top: 0;
					transform: scale(0);
					z-index: 1;
					transition: 0.3s;
				}
				&.slick-active{
					&::after{
						transform: scale(0.33);
					}
				}
			}
		}
	}
	&__item{
		display: block;
		&:hover{
			.doctors__name{
				color: #C31D1F;
			}
		}
	}
	&__image{
		position: relative;
		padding-bottom: 117%;
		display: block;
		img{
			position: absolute;
			left: 0;
			top: 0;
			z-index: 1;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		&::after{
			content: "";
			display: block;
			width: 18px;
			height: 18px;
			border: 1px solid #FFFFFF;
			box-sizing: border-box;
			position: absolute;
			z-index: 2;
			right: 10px;
			top: 10px;
			border-radius: 50%;
		}
		&--1{
			&::after{background-color: #6C2A81}
		}
		&--2{
			&::after{background-color: #1B73B2}
		}
		&--3{
			&::after{background-color: #E01A4D}
		}
	}
	&__name{
		text-align: center;
		display: block;
		font-weight: 500;
		font-size: 16px;
		line-height: 135%;
		color: #13070C;
		margin-top: 12px;
	}
	&__role{
		display: block;
		text-align: center;
		font-weight: 500;
		font-size: 13px;
		line-height: 135%;
		color: #878183;
		margin-top: 5px;
	}
}

